#myid .class_1.class_2 {
  color: red;
}

#myid.class_3 .class_4 {
  color: green;
}

/* Centering with margin */
h1 {
  width: 200px;
  margin: 5px auto;
}

#header {
  width: 250px;
  margin: 10 auto;
}

h1 {
  line-height: 50px;
  text-align: center;
}

h1:after {
  content: "after";
}

h1::before {
  content: "";
  position: absolute;
  height: 100px;
  width: 100px;
  background-color: red;
}

div a {
  text-decoration: none;
  color: red;
}

div a:hover {
  text-decoration: none;
  color: black;
}
